* {
  font-family: Verdana, Tahoma, sans-serif;
  color: rgb(34, 34, 34)
}

body {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #fafafa;
}

p, input {
  font-size: 16px;
}

.app {
  height: 80vh;
  width: 80vw;
  background-color: rbg(255, 255, 255);
  border-radius: 10px;
  box-shadow: rgba(0,0,0, 0.16) 0 1px 4px;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.code-display {
  width: 96%;
  margin: 2%;
  height: 46%;
  border-radius: 8px;
  background-color: rgb(34, 34, 34);
  overflow: hidden;

}

.buttons {
  width: 100%;
  height: 35px;
  background-color: rgb(50,50,50);
  display: flex;
  align-items: center;
  padding-left: 5px;
}

.button {
  height: 15px;
  width: 15px;
  border-radius: 50%;
  margin: 4px;
  background-color: red;
}

.button.first {
  background-color: rgb(255,96, 86);
}

.button.middle {
  background-color: rgb(255,189, 45);
}

.button.last {
  background-color: rgb(38,201, 64);
}

.messages-display {
  width: 96%;
  margin: 2%;
  height: 46%;
  overflow-y: scroll;

}

::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 10px;
}

::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: rgba(0,0,0,.5);
  -webkit-box-shadow: 0 0 1px rgba(255,255,255,.5);
}


.message-display {
  width: 100%;
  display: flex;
  border-radius: 5px;
  box-sizing: border-box;
  background-color:#f1efef;
  margin: 5px 0;

}

p#icon {
  padding: 0 7px;

}

input {
  padding: 16px;
  margin: 2%;
  border: solid rgba(0,0,0, 0.16) 1px;
  box-sizing: border-box;
  border-radius: 5px;

}

.button-container {
  margin: 2%;
  display: flex;
  justify-content: flex-end;

}

.button-container button {
  text-transform: uppercase;
  padding: 7px;
  border: none;
}

button#get-query {
  background-color: rgb(255, 95, 86);
  border-radius: 10px 0 0 10px;
}

button#clear-chat {
  background-color: rgb(255, 189, 45);
  border-radius: 0 10px 10px 0;
}